:root {
  --color-black: #000000;
  --color-white: #ffffff;
  --body-color: var(--front-primary);
  --body-bg: var(--back-primary);
  --font-base: var(--font-sans);
  --font-sans:
    Onest, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --back-blur-header: 12px;
  --box-shadow-default: 0 4px 6px -2px rgba(0 0 0 / 5%), 0 10px 15px -3px rgba(0 0 0 / 10%);
  --white-gradient: linear-gradient(180deg, rgba(255, 255, 255, 0) 0, var(--white) 100%);
  --transition-default: 0.25s ease;
  --z-index-panel: 5;
  --z-index-darker: 4;
  --z-index-cart: 3;
  --z-index-header: 2;
  --white: #ffffff;
  --white-transparent: rgba(255 255 255 / 95%);
  --zinc-100: #f4f4f5;
  --zinc-200: #e4e4e7;
  --zinc-300: #d4d4d8;
  --zinc-400: #a1a1aa;
  --zinc-700: #3f3f46;
  --zinc-700-25p: rgba(63 63 70 / 25%);
  --orange-500: #f97316;
  --orange-600: #ea580c;
  --purple-300: #d8b4fe;
  --purple-600: #9333ea;
  --purple-700: #7e22ce;
  --vk-brand-color: #4680c2;
  --vk-brand-hover: #22558e;
  --error: #e30000;
  --brand-primary: var(--purple-700);
  --front-primary: var(--zinc-700);
  --front-invert: var(--white);
  --back-primary: var(--white);
  --header-height: 140px;
}
@media screen and (max-width: 1279px) {
  :root {
    --header-height: 160px;
  }
}
@media screen and (max-width: 991px) {
  :root {
    --header-height: 140px;
  }
}
@media screen and (max-width: 767px) {
  :root {
    --header-height: 105px;
  }
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
