.checkbox {
  position: relative;
  display: flex;
  gap: 8px;
  justify-self: start;
}
.checkbox__input {
  position: absolute;
  width: 16px;
  height: 16px;
  opacity: 0;
}
.checkbox__fake-input {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  background: var(--white);
  border: 1px solid var(--zinc-200);
  border-radius: 4px;
  cursor: pointer;
  transition: background var(--transition-default);
}
.checkbox__fake-input::after {
  display: block;
  width: 10px;
  height: 6px;
  border: 2px solid transparent;
  border-top: none;
  border-right: none;
  transform: translate(4px, 5px) rotate(-45deg);
  transition: border-color var(--transition-default);
  content: "";
}
.checkbox__input:checked + .checkbox__fake-input {
  background: var(--brand-primary);
}
.checkbox__input:checked + .checkbox__fake-input::after {
  border-color: var(--front-invert);
}
.checkbox__label {
  font-size: 15px;
  line-height: 1.3334;
  color: var(--zinc-700);
  cursor: pointer;
  user-select: none;
}

.collections__tiles {
