}
:is(.form) .form__error[data-tooltip=true] {
  padding: 4px 14px;
  color: var(--error);
  background: var(--back-primary);
  border: 1px solid var(--zinc-300);
  border-radius: 15px;
  transform: translateY(-5px);
}
